
#attempts {
    position: relative;
    bottom: 10%;
}
.mini-button {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    border: 3px dotted black;
    margin-left: 10px;
}
.attempt-row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-top: 10px;
    width: 100%;
}
@media screen and (max-width: 900px) {
.mini-button {
        width: 50px;
        height: 50px;
}
}



