
.guess-button {
    width: 14%;
    margin-left: 5%;
    margin-top: 3%;
    left: 0%;
}
.main {
    display : -webkit-box;
    display : -ms-flexbox;
    display : flex;
    width: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
}
.guess-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 75%;
    height: 175px;
    background-color: grey;
    border: 3px solid black;
    border-radius: 60px;
}
.contain {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 100%;
}
.selection-container {
    position: relative;
    margin-left: 10%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    right: 20%;
    width: 30%;
}
.status-container {
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    width: 55%;
    position: relative;
}
.status {
    font-size: 1.75em;
    margin-top: 3%;
}
.option {
    font-size: 2.5em;
    margin-top: 1%;
}
.option:hover {
    cursor: pointer;
}
.check {
    color: green;
}
.fill-in {
    -webkit-animation: fill-in .25s 1 ease-in forwards;
            animation: fill-in .25s 1 ease-in forwards;
}
.fade-out {
    -webkit-animation: fill-out .25s 1 ease-in forwards;
            animation: fill-out .25s 1 ease-in forwards;
}
@-webkit-keyframes fill-in {
from {
        -webkit-transform: scale(0);
                transform: scale(0);
}
to {
        -webkit-transform: scale(1);
                transform: scale(1);
}
}
@keyframes fill-in {
from {
        -webkit-transform: scale(0);
                transform: scale(0);
}
to {
        -webkit-transform: scale(1);
                transform: scale(1);
}
}
@-webkit-keyframes fill-out {
from {
        -webkit-transform: scale(1);
                transform: scale(1);
}
to {
        -webkit-transform: scale(0);
                transform: scale(0);
}
}
@keyframes fill-out {
from {
        -webkit-transform: scale(1);
                transform: scale(1);
}
to {
        -webkit-transform: scale(0);
                transform: scale(0);
}
}

/* MEDIA QUERIES */
@media screen and (max-width: 1399px) {
.option {
        font-size: 2em;
}
}
@media screen and (max-width: 900px) {
.guess-container {
        width: 95%;
}
.selection-container {
        right: 10%;
        margin-top: 1%;
}
.status-container {
        width: 75%;
        margin-bottom: 3%;
}
.option {
        font-size: 2em;
        margin-left: 25%;
}
}
@media screen and (max-width: 600px) {
.guess-button {
        height: 50px;
        width: 9%;
        left: 3%;
}
.guess-container {
        width: 100%;
        height: 125px;
}
.selection-container {
        margin-top: 0%;
}
}
@media screen and (max-width: 500px) {
.guess-button {
        width: 7.5%;
        height: 45px;
        left: 2%;
}
.guess-container {
        width: 100%;
        height: 100px;
}
.selection-container {
        margin-top : 1%;
        right: 7%;
}
.option {
        font-size: 1.5em;
}
}




