
.button {
    position: relative;
    width: 7%;
    right: 15%;
    height: 100px;
    border: 5px solid black;
    border-radius: 50%;
    margin: 30px;
    margin-top: -20px;
    -webkit-transition: all .25s;
    transition: all .25s;
    box-shadow: 0 10px black;
    z-index: 1;
}
.button:hover {
    cursor: pointer;
    -webkit-transform: rotateX(30deg);
            transform: rotateX(30deg);
}
.button:active {
    box-shadow: 0 0;
}
@media screen and (max-width: 1399px) {
.button {
        width: 5%;
        height: 80px;
}
}
@media screen and (max-width: 900px) {
.button {
        width: 10%;
        left: 0%;
}
}
@media screen and (max-width: 600px) {
.button {
        width: 9%;
        height: 66px;
}
}




